.container h1 {
  text-align: center;
}
.feedback {
  display: flex;
  flex-direction: column;
}
.feedback input {
  border: 1px solid black;
  border-radius: 20px;
  font-size: 20px;
  padding: 5px 10px;
  box-sizing: border-box;
}

.feedback label {
  margin: 10px auto;
  font-size: 20px;
}
.feedback button {
  border: 2px solid black;
  border-radius: 25px;
  padding: 10px 40px;
  font-size: 20px;
  margin: 20px auto;
  width: fit-content;
}
.newFeedback {
  margin: 15px auto;
  border: 3px solid black;
}

.newFeedback {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.box {
  display: flex;
  justify-content: center;
}
.table{
  width: 100%;
  border-collapse: collapse;
}
.table  td{
  border: 1px solid black;
}
